.main-banner {
  display: grid;
  color: $secondary-text-color;
}

.main-banner__content {
  z-index: 2;
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  padding: 30px;
  margin: 0 auto;
  text-align: center;

  @media (min-width: $tablet-width) {
    display: flex;
    max-width: 280px;
    flex-direction: column;
    align-items: center;
  }

  @media (min-width: $laptop-width) {
    max-width: 400px;
  }

  @media (min-width: $wide-desktop-width) {
    max-width: none;
    justify-content: center;
  }
}

.main-banner__logo {
  width: 128px;
  margin: 191px 0 11px;

  @media #{$width-666} {
    margin-top: 335px;
  }

  @media (min-width: $tablet-width) {
    width: 184px;
    margin-top: 0;
    margin-bottom: 46px;
  }

  @media (min-width: $wide-tablet-width) {
    margin-bottom: 67px;
  }

  @media (min-width: $laptop-width) {
    width: 260px;
    margin-bottom: 81px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 99px;
  }

  @media (min-width: $wide-desktop-width) {
    margin-bottom: 42px;
  }

  @media (min-width: 2561px) {
    width: calc(515px - (5120px - 100vw) * ((515 - 260) / 2560));
    margin-bottom: calc(90px - (5120px - 100vw) * ((90 - 42) / 2560));
  }

  @media (min-width: $wide-desktop-2-width) {
    width: 515px;
    margin-bottom: 90px;
  }
}

.main-banner__heading {
  margin: 0 0 115px;

  @media (min-width: $tablet-width) {
    order: -1;
    margin: 204px 0 10px;
  }

  @media (min-width: $wide-tablet-width) {
    margin-top: 241px;
  }

  @media (min-width: $laptop-width) {
    margin-top: 303px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 393px;
  }

  @media (min-width: $wide-desktop-width) {
    margin-top: 225px;
    margin-bottom: 22px;
  }

  @media (min-width: 2561px) {
    margin-top: calc(450px - (5120px - 100vw) * ((450 - 225) / 2560));
    margin-bottom: calc(46px - (5120px - 100vw) * ((46 - 22) / 2560));
  }

  @media (min-width: $wide-desktop-2-width) {
    margin-top: 450px;
    margin-bottom: 46px;
  }
}

.main-banner .main-banner__heading {
  @media (min-width: $tablet-width) {
    font-size: 24px;
    line-height: 1;
  }

  @media (min-width: $laptop-width) {
    font-size: 36px;
  }

  @media (min-width: $wide-desktop-width) {
    font-size: 48px;
  }

  @media (min-width: 2561px) {
    font-size: 1.875vw;
  }

  @media (min-width: $wide-desktop-2-width) {
    font-size: 96px;
  }
}

.main-banner__link {
  padding-right: 16px;
  padding-left: 16px;
  border-width: 1px;
  font-size: 14px;
  letter-spacing: 0.05em;

  @media (min-width: $tablet-width) {
    padding-right: 20px;
    padding-left: 20px;
  }

  @media (min-width: $laptop-width) {
    padding-right: 29px;
    padding-left: 29px;
  }

  @media (min-width: 1898px) {
    min-width: 142px;
    height: 36px;
    margin-top: 0;
    margin-bottom: 45px;
    line-height: 32px;
  }

  @media (min-width: $wide-desktop-width) {
    margin-bottom: 0;
  }

  @media (min-width: 2561px) {
    height: calc(72px - (5120px - 100vw) * ((72 - 36) / 2560));
    max-height: 72px;
    padding-right: calc(70px - (5120px - 100vw) * ((70 - 29) / 2560));
    padding-left: calc(70px - (5120px - 100vw) * ((70 - 29) / 2560));
    font-size: 0.546875vw;
    line-height: 1.30859375vw;
  }

  @media (min-width: $wide-desktop-2-width) {
    height: 72px;
    padding-right: 70px;
    padding-left: 70px;
    font-size: 28px;
    line-height: 67px;
  }
}

.main-banner__img-wrapper {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  text-align: center;
  background-color: rgb(80, 99, 123);
}

.main-banner__img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
